import { RegistrationParams } from 'api/services/user';
import { RegistrationFormErrors } from 'pages/RegistrationPage/RegistrationPage.types';

export const validateRegistrationForm = (values: RegistrationParams): RegistrationFormErrors => {
    const errors: RegistrationFormErrors = {
        username: undefined,
        password: undefined,
        email: undefined,
        birth_date: undefined,
        sex: undefined,
    };

    if (!values.username) {
        errors.username = '   ';
    }

    if (!values.password) {
        errors.password = '  ';
    }

    if (!values.email) {
        errors.email = '  email';
    }

    if (!values.birth_date) {
        errors.birth_date = '   ';
    }

    if (!values.sex) {
        errors.sex = '   ';
    }

    return errors;
};
